function setID(val, prefix, value) {
    const newState = Object.assign({}, val);
    if (value) {
        newState.ids[prefix] = value;
        return newState;
    }
    if (!Object.keys(newState.ids).includes(prefix))
        newState.ids[prefix] = 0;
    newState.ids[prefix]++;
    return newState;
}
export function generateID(val, prefix) {
    setID(val, prefix);
    return `${prefix}_${val.ids[prefix]}`;
}
export function defaultState() {
    return {
        timers: {},
        ids: {}
    };
}
export function defaultTimer(n) {
    return {
        name: `Timer ${n}`,
        length: 60,
        currentTime: 60,
        active: -1
    };
}
