import { writable } from "svelte/store";
import { defaultState, defaultTimer, generateID } from './Defaults';
const storedTheme = localStorage.getItem('state');
export const state = writable(storedTheme ?
    JSON.parse(storedTheme) :
    defaultState());
let defaultTimerNumber = 0;
let currentStateAsJSON;
state.subscribe(val => {
    currentStateAsJSON = toJSON(val);
    localStorage.setItem('state', currentStateAsJSON);
});
function toJSON(val) {
    return JSON.stringify(Object.assign(Object.assign({}, val), { type: "state" }));
}
export function exportState() {
    return new Blob([currentStateAsJSON], { type: 'text/json' });
}
export function refreshTimers(val) {
    const newState = Object.assign({}, val);
    for (const timer of Object.values(newState.timers)) {
        timer.currentTime = timer.length;
        timer.active = -1;
    }
    return newState;
}
export function addDefaultTimer(val) {
    defaultTimerNumber++;
    const newState = Object.assign({}, val);
    newState.timers[generateID(val, 'Timer')] = defaultTimer(defaultTimerNumber);
    return newState;
}
export function editTimerLength(val, timerID, length) {
    const newState = Object.assign({}, val);
    const timer = newState.timers[timerID];
    if (timer.currentTime === timer.length)
        timer.currentTime = length;
    timer.length = length;
    return newState;
}
export function editTimerTitle(val, timerID, title) {
    const newState = Object.assign({}, val);
    newState.timers[timerID].name = title;
    return newState;
}
export function removeTimer(val, timerID) {
    const newState = Object.assign({}, val);
    delete newState.timers[timerID];
    return newState;
}
export function decrementTimer(val, timerID) {
    const newState = Object.assign({}, val);
    const timer = newState.timers[timerID];
    if (timer.currentTime === 0) {
        timer.active = -1;
        timer.currentTime = timer.length;
        return newState;
    }
    timer.currentTime--;
    return newState;
}
export function controlTimer(val, timerID, active) {
    const newState = Object.assign({}, val);
    const timer = newState.timers[timerID];
    timer.active = active;
    return newState;
}
